@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
html {
  overflow: hidden;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
}
#root {
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(245, 245, 245, 0.705);
}
header {
  position: absolute;
  top: 0;
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
h1 {
  font-weight: 570;
  font-size: 20px;
  letter-spacing: 1px;
  transition: 0.3s ease;
}
h1:hover {
  letter-spacing: 3px;
}
main {
  width: 400px;
  height: 500px;
  border: 1px solid #111;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
}
.Box-add {
  width: 100%;
  height: 20%;
  border-bottom: 1px solid #777;
  display: flex;
  padding: 10px;
}
.Box-input {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 8px;
}
.Box-input input {
  width: 100%;
  padding-left: 10px;
  height: 40px;
  border-radius: 2px;
  font-size: 17px;
  border: 1px solid #666;
  outline: 1px solid #7c9ee9;
  transition: 0.2s ease;
}
.Box-input input:hover,
.Box-input input:focus {
  border: 1px solid #7c9ee9;
}
.Box-btn {
  width: 20%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  position: relative;
}
.Box-btn button {
  width: 80px;
  height: 40px;
}
.List-container {
  width: 100%;
  height: 75%;
  flex-direction: column;
  display: flex;
  overflow: scroll;
  overflow-x: hidden;
}
.List {
  display: flex;
  padding: 12px;
  padding-right: 10px;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
  position: relative;
  min-height: 95%;
  margin: 3px;
}
.todo {
  width: 100%;
  height: 47px;
  padding: 3px;
  display: flex;
}
@media only screen and (max-width: 450px) {
  main {
    width: 325px;
    height: 406.25px;
  }
  .Box-input input {
    width: 100%;
    padding-left: 10px;
    height: 37px;
    border-radius: 2px;
    outline: 1px solid #7c9ee9;
  }
  .Box-btn button {
    width: 65px;
    height: 37px;
  }
}

